	NICK'S MIXINS
***********************/
/*******************************/
/* Horizontal navigation list */
/*
Write HTML like:
ul {
    color:red;
    li {
        font-weight:bold;
        
        a {
	        color: blue;
	        &:hover {
		        color: red;
	        }
        }
    }
}

CSS:
ul.main-nav {
	@include navigation-list;
}
