button {
  position: relative;
  overflow: hidden; }

button:after {
  content: '';
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 325px;
  height: 325px;
  margin-left: -162px;
  margin-top: -162px;
  background: #00b3e3;
  border-radius: 100%;
  opacity: .6;
  transform: scale(0); }

